<template>
  <div class="payment_container">
      <head-top head-title="在线支付" goBack="true"></head-top>
        <section class="payment_timer">
            <section>
                <header>支付剩余时间</header>
                <span>{{remaining}}</span>
            </section>
        </section>
        <section class="payment_fangshi">
            <header>选择支付方式</header>
            <section class="paymenr_zifu">
                <section class="payment_item">
                    <div class="pay_icon_contaienr">
                        <div class="zifubao"></div>
                        <span>支付宝</span>
                    </div>
                </section>
                <svg class="choose_icon" @click="payWay = 1" :class="{choosed_way: payWay == 1}">
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                </svg>
            </section>
            <section class="paymenr_zifu">
                <section class="payment_item">
                    <div class="pay_icon_contaienr">
                        <div class="weixing"></div>
                        <span>微信</span>
                    </div>
                </section>
                <svg class="choose_icon" @click="payWay = 2" :class="{choosed_way: payWay == 2}">
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                </svg>
            </section>
        </section>
        <section class="pay_buttom">
            <div class="buttom" @click="confrimPay">确认支付</div>
        </section>
      <alert-tip v-show="showAlert" @closeTip="closeTipFun" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import headTop from '../../../components/header/header'
import {mapState,mapMutations} from 'vuex';
import {payRequest} from '../../../service/getData'
import alertTip from '../../../components/common/alertTip'
export default {
    components: {
        headTop,
        alertTip
    },created () {
        this.initDate();
        if(this.shopid)
        {
            this.CLEAR_CART(this.shopid);
        }
    },
    mounted () {
        this.remainingTime();
    },
    //组件销毁的时候
    beforeDestroy () {
        clearInterval(this.timer);
    },
    data(){
        return{
            payDetail:false,//付款信息详情
            showAlert:false,
            alertText:null,
            payWay:1,//付款方式
            countNum:900,//倒计时15分钟
            gotoOrders:false,//去付款
        }
    },methods: {
        ...mapMutations([
            'CONFIRM_INVOICE', 'CLEAR_CART'
        ]),
        //初始化信息
        async initDate()
        {
            //alert(JSON.stringify(this.orderMessage))
            this.payDetail=await payRequest(this.orderMessage.order_id,this.userInfo.user_id);
             if (this.payDetail.message) {
                    this.showAlert = true;
                    this.alertText = this.payDetail.message;
                    return
            }
        },
        //倒计时
        remainingTime()
        {
            //清除定时器
            clearInterval(this.timer);
            this.timer=setInterval(()=>{
                this.countNum--;
              //  console.log("加湿器");
                if(this.countNum==0)
                {
                    clearInterval(this.timer);
                    alertText="支付超时";
                    showAlert=true;
                }
            },1000)
        },
        //确认付款
        confrimPay(){
            this.showAlert=true;
            this.alertText="当前环境无法支付,请打开app支付";
            this.gotoOrders=true;
        },
        //关闭提示框，跳转到订单列表页
        closeTipFun(){
            this.showAlert=false;
            if(this.gotoOrders)
            {
               this.$router.push('/order');
            }
        }
    },computed: {
        ...mapState([
            'orderMessage', 'userInfo', 'shopid', 'cartPrice'
        ]),
        //时间转换
        remaining:function()
        {
            let minute=parseInt(this.countNum/60);
            console.log(minute);
            if(minute<10)
            {
                minute='0'+minute;
            }
            let second=parseInt(this.countNum%60);
            if(second<10)
            {
                second='0'+second;
            }
            return '00 : '+minute+' : '+second;
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.payment_container{
    position: fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    z-index: 104;
    background-color:#f5f5f5;
}
.payment_timer{
    margin-top: 2rem;
    background-color: #fff;
    padding:.7rem;
    section{
        text-align: center;
        padding-top: 0.4rem;
        header{
            font-size: 0.6rem;
        }
        span{
            line-height: 2.8rem;
            font-size: 1.5rem;
        }
    }
}
.payment_fangshi{
    margin-top: 0.2rem;
    header{
        padding:0.5rem;
        font-size: 0.8rem;
    }
    .paymenr_zifu{
        background: #fff;
        display: flex;
        justify-content: space-between;
        padding:0.8rem;
        align-items: center;
        border-bottom: 0.025rem solid $bc;
        svg{
            @include wh(1rem,1rem);
            fill: #666;
        }
        .choosed_way{
            fill: #4cd964;
        }
        .payment_item{
            .pay_icon_contaienr{
                display: flex;
                align-items: center;
                span{
                    font-size: 0.8rem;
                    color:#666;
                }
            }
        }
    }
}
.zifubao{
    @include wh(2rem,2rem);
    background:url(../../../images/zhifubao.png) no-repeat;
    background-size: 100% 100%;
    margin-right: .2rem;
}
.weixing{
    @include wh(2rem,2rem);
    background: url(../../../images/weixin.png) no-repeat;
    background-size: 100% 100%;
    margin-right: .2rem;
}
.pay_buttom{
    margin-top: 0.4rem;
    .buttom{
        background: #4cd964;
        text-align: center;
        @include sc(0.8rem,#fff);
        padding:0.4rem;
        margin:0.5rem;
        border-radius: 0.2rem;
    }
}
</style>



